<template>
  <div
    style="height: 100%;padding: 15%;min-width: 1200px"
    :style="{backgroundImage: 'url(' + imgbg + ')', backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100% '}">
  <div class="main user-layout-register" >
    <a-row type="flex" justify="center">
      <a-col :span="6">
        <h3>
          <span>修改密码</span>
        </h3>
        <a-form ref="formRegister" :form="form" id="formRegister">
          <a-form-item>
                  <a-select
                    v-decorator="[
                    'user_type',
                    {rules: [{ required: true, message: '请选择身份' }]}
                  ]"
                    placeholder="请选择身份">
                    <a-select-option value="ent">企业用户</a-select-option>
                    <a-select-option value="mag">管理员</a-select-option>
                    <a-icon slot="prefix" type="user" :style="{ color: 'rgba(0,0,0,.25)' }"/>
                  </a-select>
                </a-form-item>
          <a-form-item>
            <a-input
              size="large"
              type="text"
              placeholder="用户名"
              v-decorator="['username', {rules: [{ required: true, message: '请输入用户名' }], validateTrigger: ['change', 'blur']}]"
            ></a-input>
          </a-form-item>
          <a-popover
            placement="rightTop"
            :trigger="['focus']"
            :getPopupContainer="(trigger) => trigger.parentElement"
            v-model="state.passwordLevelChecked"
          >
            <template slot="content">
              <div :style="{ width: '240px' }">
                <div :class="['user-register', passwordLevelClass]">
                  强度：
                  <span>{{ passwordLevelName }}</span>
                </div>
                <a-progress
                  :percent="state.percent"
                  :showInfo="false"
                  :strokeColor=" passwordLevelColor "
                />
                <div style="margin-top: 10px;">
                  <span>请至少输入 6 个字符。请不要使用容易被猜到的密码。</span>
                </div>
              </div>
            </template>
            <a-form-item>
              <a-input
                size="large"
                type="password"
                autocomplete="false"
                placeholder="至少6位密码，区分大小写"
                v-decorator="['password', {rules: [{ required: true, message: '至少6位密码，区分大小写'}, { validator: this.handlePasswordLevel }], validateTrigger: ['change', 'blur']}]"
              ></a-input>
            </a-form-item>
          </a-popover>
          <a-form-item>
            <a-button
              size="large"
              type="primary"
              htmlType="submit"
              class="register-button"
              :loading="registerBtn"
              @click.stop.prevent="handleSubmit"
              :disabled="registerBtn"
            >修改密码</a-button>
            <router-link class="login" to="/login">使用已有账户登录</router-link>
          </a-form-item>
        </a-form>
      </a-col>
    </a-row>
  </div>
  </div>
</template>

<script>
import { recover } from '@/api/login'
const levelNames = {
  0: '低',
  1: '低',
  2: '中',
  3: '强'
}
const levelClass = {
  0: 'error',
  1: 'error',
  2: 'warning',
  3: 'success'
}
const levelColor = {
  0: '#ff0000',
  1: '#ff0000',
  2: '#ff7e05',
  3: '#52c41a'
}
export default {
  name: 'Recover',
  components: {},
  data () {
    return {
      form: this.$form.createForm(this),

      state: {
        time: 60,
        smsSendBtn: false,
        passwordLevel: 0,
        passwordLevelChecked: false,
        percent: 10,
        progressColor: '#FF0000'
      },
      registerBtn: false,
      imgbg:require('../../assets/dddb-webEnt/A2.1-changecode-bg.png'),
    }
  },
  computed: {
    passwordLevelClass () {
      return levelClass[this.state.passwordLevel]
    },
    passwordLevelName () {
      return levelNames[this.state.passwordLevel]
    },
    passwordLevelColor () {
      return levelColor[this.state.passwordLevel]
    }
  },
  methods: {
    handlePasswordLevel (rule, value, callback) {
      let level = 0

      // 判断这个字符串中有没有数字
      if (/[0-9]/.test(value)) {
        level++
      }
      // 判断字符串中有没有字母
      if (/[a-zA-Z]/.test(value)) {
        level++
      }
      // 判断字符串中有没有特殊符号
      if (/[^0-9a-zA-Z_]/.test(value)) {
        level++
      }
      this.state.passwordLevel = level
      this.state.percent = level * 30
      if (level >= 2) {
        if (level >= 3) {
          this.state.percent = 100
        }
        callback()
      } else {
        if (level === 0) {
          this.state.percent = 10
        }
        callback(new Error('密码强度不够'))
      }
    },

    handleSubmit () {
      const {
        form: { validateFields },
        state
        // $router
      } = this
      validateFields({ force: true }, (err, values) => {
        if (!err) {
          const registerParams = { ...values }
          const that = this
          state.passwordLevelChecked = false
          recover(registerParams).then(function (response) {
            if (response.data.result === 25) {
              alert('修改密码成功，请重新登录')
              that.$router.push({ path: '/login' })
            } else if (response.data.result === 40) {
              alert('修改失败，用户名不存在')
            }
          })
        }
      })
    }
  },
  watch: {
    'state.passwordLevel' (val) {
    }
  }
}
</script>
<style lang="less">
.user-register {
  &.error {
    color: #ff0000;
  }

  &.warning {
    color: #ff7e05;
  }

  &.success {
    color: #52c41a;
  }
}

.user-layout-register {
  .ant-input-group-addon:first-child {
    background-color: #fff;
  }
}
</style>
<style lang="less" scoped>
.user-layout-register {
  & > h3 {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .getCaptcha {
    display: block;
    width: 100%;
    height: 40px;
  }

  .register-button {
    width: 50%;
  }

  .login {
    float: right;
    line-height: 40px;
  }
}
</style>



